@charset 'UTF-8';

@import "variables";


// Editor
//
@import "editor/width";
@import "editor/inserter";
@import "editor/inspector";
@import "editor/panel";
@import "editor/picker-gradient";
@import "editor/utility";



// Toolbar
//
@import "toolbar/toolbar";
@import "toolbar/divider";
@import "toolbar/url-input";
@import "toolbar/color-palette";
@import "toolbar/more-option";
@import "toolbar/range-control";
@import "toolbar/dropdown-menu";
@import "toolbar/icon-picker";


.editor-styles-wrapper {
  min-height: auto !important;
  
  .bg-video {
    z-index: 0 !important;
  }
}

.components-toolbar__control.components-button.bgImageUploader {
  position: absolute;
  top: 0.5rem;
  left: 0.5rem;
  background: #fff;
  opacity: 0.5;
  transition: 0.3s ease-out;

  &:hover {
    opacity: 1;
  }
}


.card.card-body {
  padding: 1.25rem;
}


.slider-items-wrapper {
  overflow-x: auto;
  display: flex;
  flex-wrap: nowrap;
  padding: 0.5rem;

  > * {
    flex: 1 0 100%;
    padding: 1rem;
  }

  &.slides-num-2 > * {
    flex-basis: 50%;
  }

  &.slides-num-3 > * {
    flex-basis: 33.333333%;
  }

  &.slides-num-4 > * {
    flex-basis: 25%;
  }

  &.slides-num-5 > * {
    flex-basis: 20%;
  }
}


.form-control {
  border: 1px solid #eaeff4 !important;
  padding: .375rem .75rem !important;
  font-size: .9375rem !important;
  line-height: 1.9 !important;
  border-radius: 2px !important;

  &:focus {
    box-shadow: none !important;
    outline: 0 !important;
  }
}


.dynamic-placeholder {
  background-color: rgba( #fdefc8, 0.2 );
}


.accordion .card {
  margin-top: 0 !important;
  max-width: none;
}
