
.the-gradient-picker-modal {
  max-width: 415px;
}

.the-gradient-picker-button {
  display: flex;
  align-items: center;
  //justify-content: space-between;
  cursor: pointer;

  label {
    margin-bottom: 0;
    flex: 1;
  }

}

.the-gradient-picker-item {
  display: inline-block;
  width: 64px;
  height: 64px;
  margin: 6px;
  border-radius: 4px;
  cursor: pointer;
  transition: 0.3s ease-out;

  &.selected {
    border-radius: 50%;
  }

  &.is-small {
    width: 28px;
    height: 28px;
  }

  &:hover {
    box-shadow: 0 0 20px rgba(0,0,0,0.1);
  }
}
