
.the-toolbar .components-dropdown-menu__popover:not(.is-mobile) .components-popover__content {
  min-width: 150px;
}

.components-dropdown-menu__menu-item.is-active {
  background-color: $color-bg-dark !important;
  color: $color-text-dark;

  &:hover {
    box-shadow: none !important;
    background-color: $color-bg-dark !important;
    cursor: default;
  }
}

.components-dropdown-menu.no-caret .components-dropdown-menu__indicator {
  display: none;
}
