.the-toolbar {
  position: fixed;
  transform: translateY(-100%);
  margin-top: -8px;
  opacity: 0;
  width: auto;
  box-shadow: 0 0 25px rgba(0,0,0,0.15);
  //transition: 0.3s ease-out;
  z-index: 99;

  &.is-visible {
    opacity: 1;
  }
}


[data-show-toolbar]:hover {
  box-shadow: 0 0 1px rgba(0,133,186,1);
  transition: 0.1s ease-out;
}

.the-toolbar-target {
  //border: 1px solid $color-divider-light;
  //border-radius: 2px;
  box-shadow: 0 0 1px rgba(226,228,231,1);
  //transform: scale(1.03,1.03);
  transition: 0.1s ease-out;

  &:hover {
    box-shadow: 0 0 1px rgba(226,228,231,1);
  }
}

.components-toolbar__control.rotate-90 .dashicon {
  transform: rotate(90deg);
}


.button-group-btn-sizes {
  margin: 16px 24px;
}

.components-toolbar__control.components-button > svg {
  width: auto;
  height: auto;
}

