

.editor-block-types-list__item[class*="editor-block-list-item-thesaasx-"] {
  background-size: contain;
  background-position: top center;
  background-repeat: no-repeat;
  border: 1px solid #f6f7f8;

  .editor-block-types-list__item-icon {
    opacity: 0;
  }

  .editor-block-types-list__item-title {
    background-color: rgba(0,0,0,0.3);
    color: #fff;
    letter-spacing: 1px;
    padding-bottom: 4px;
    padding-top: 2px;
    margin-top: 6px;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    opacity: 0.6;
    transition: 0.3s ease-out;
  }

  &:hover .editor-block-types-list__item-title {
    color: #fff !important;
    opacity: 1;
  }
}


$blocks: (
  blog:       20,
  brand:      20,
  contact:    20,
  content:    20,
  counter:    20,
  cover:      20,
  cta:        20,
  demo:       20,
  download:   20,
  explore:    20,
  faq:        20,
  feature:    20,
  featuretxt: 20,
  footer:     20,
  gallery:    20,
  header:     20,
  job:        20,
  map:        20,
  navbar:     20,
  page:       20,
  post:       20,
  portfolio:  20,
  pricing:    20,
  process:    20,
  review:     20,
  service:    20,
  signup:     20,
  subscribe:  20,
  team:       20,
  video:      20,
);

@each $name, $count in $blocks {
  @for $i from 1 through $count {
    .editor-block-list-item-thesaasx-#{$name}-#{$i} {
      background-image: url(../../gutenberg/blocks/#{$name}/block-#{$i}/poster.jpg);
    }
  }
}



