
.the-toolbar-icon-picker-popup {

  .components-base-control__field {
    margin-bottom: 0;
  }

  .components-text-control__input {
    border-radius: 0 !important;
    border: none !important;
    box-shadow: none !important;
    background-color: #f6f7f8;
    padding: 10px 16px !important;
  }

  hr {
    margin-top: 0.75rem;
    margin-bottom: 0.75rem;
  }
}

.the-toolbar-icon-picker-tools {
  display: flex;
  flex-direction: row;

  .components-base-control {
    flex: 1;
  }

  .components-button {
    padding-left: 8px;
    padding-right: 8px;
  }
}

.the-toolbar-icon-picker-items {
  padding: 8px 3px;
  height: 260px;
  overflow-y: auto;
  overflow-x: hidden;
}

.the-toolbar-icon-picker-item {
  display: inline-block;
  width: 31px;
  height: 31px;
  line-height: 31px;
  text-align: center;
  font-size: 19px;
  margin: 4px;
  border-radius: 3px;
  cursor: pointer;
  color: #545454;
  transition: 0.1s ease-out;

  &:hover {
    color: #000;
    transform: scale(1.6);
  }

  &.selected {
    //background-color: #3c96ff;
    color: #3c96ff;
    transform: scale(1.6);
  }

}

.the-toolbar-icon-picker-button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;

  i {
    font-size: 20px;
  }
}


.the-toolbar-icon-picker-group {

  .group-title {
    margin: 4px 12px;
    font-size: 12px;
    font-weight: 600;
    letter-spacing: 1px;
    text-transform: uppercase;
    cursor: pointer;
  }

}
