

// Upload box
//
.the-media-upload {
  display: flex;
  align-items: center;
  justify-content: center;
  background: $color-bg;
  border: 1px dashed $color-divider;
  padding: 4px;
  cursor: pointer;
  font-size: 13px;
  text-align: center;
  min-height: 80px;
}


// Equal sized row
//
.the-row {
  display: flex;
  justify-content: space-between;
  align-items: center;

  > .components-base-control {
    width: 47%;
    margin-bottom: 0 !important;
  }
}


// Color picker
//
.components-button.is-link.components-color-palette__clear {
  margin-right: 12px;
  margin-top: 4px;
}

.components-color-palette {
  margin-right: -11px;
}

.components-color-palette__item-wrapper {
  margin-right: 11px;
  margin-bottom: 11px;
}
.components-color-palette__clear {
  margin-top: 2px;
}

.the-color-palette {
  margin-bottom: -8px;

  .components-base-control__label {
    margin-bottom: 10px;
  }
}


// Double button group, for YouTube and Vimeo selector
//
.btn-group-double {
  width: 100%;
  margin-bottom: 20px;

  .components-button {
    width: 50%;
    justify-content: center;
  }
}
